import { useEffect, useState } from "react";
import { useGetUserShiftSlotsQuery, useGetUsersPerShiftQuery } from "../UserShiftsApi";
import { useDispatch } from "react-redux";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";
import CustomFormDialog from "app/shared-components/custom-form-dialog/CustomFormDialog";
import { BookingStatus, IBooking } from "../../booking/BookingModel";
import { z } from "zod";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { useTranslation } from "react-i18next";
import { BOOKING_PAGE, CONSTANTS, SETTINGS_PAGE, SHIFTS_PAGE, USER_SHIFTS_PAGE } from "app/constants/TranslationNamespaces";
import { closeDialog, openDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import moment from "moment";
import { useGetOneShiftQuery } from "../../shifts/tabs/shifts/ShiftsApi";
import { useGetBusinessInfoQuery } from "../../settings/tabs/business-info/BusinessInfoApi";
import { IShift } from "../../shifts/tabs/shifts/ShiftsModel";
import { IBusinessInfo } from "../../settings/tabs/business-info/BusinessInfoModel";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { SelectListShiftsShort } from "app/constants/SelectListShifts";
import { IUserPerShiftArg, IUserShiftSlotArg, IUserShiftSlotDay } from "../UserShiftsModel";
import { Typography } from "@mui/material";
import { CARDINAL_500, VIOLET_500 } from "app/constants/DefaultColors";
import CustomTable from "app/shared-components/custom-table/CustomTable";
import { userPerShiftHeadings } from "../constants/usersPerShiftHeadings";
import { UsersPerShiftTableCustomIcons } from "../users-per-shift-table-custom-icons/UsersPerShiftTableCustomIcons";
import CustomTextField from "app/shared-components/custom-text-field/CustomTextField";
import useWindowDimensions from "app/hooks/useWindowDimensions";
import { useGetOneBookingQuery } from "../../booking/BookingApi";
import BookingInformationDialog from "../../booking/booking-information-dialog/BookingInformationDialog";
import BookingCancelDialog from "../../booking/booking-cancel-dialog/BookingCancelDialog";

type UserShiftsDialogFormProps = {
	day: string;
	shiftId: string;
	businessId: string;
	isDay: boolean;
	isNight: boolean;
	isHoliday: boolean;
	bookingId?: string;
	status?: string;
	endDate?: string;
};

const UserShiftsDialogForm = (props: UserShiftsDialogFormProps) => {
	const { width } = useWindowDimensions();
	const { day, shiftId, businessId, isDay, isNight, isHoliday, bookingId, endDate, status } = props;
	const [dayValue, setDay] = useState<Date>(moment(day, "yyyy-MM-DD").toDate());
	const [title, setTitle] = useState<string>();
	const bookingTranslation = useTranslation(BOOKING_PAGE);
	const constantTranslation = useTranslation(CONSTANTS);
	const userShiftsTranslation = useTranslation(USER_SHIFTS_PAGE);
	const shiftsTranslation = useTranslation(SHIFTS_PAGE);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const dispatch = useDispatch();
	const [usersPerShiftArgs, setUsersPerShiftArgs] = useState<IUserPerShiftArg>({ day: moment(dayValue).format("yyyy-MM-DD"), businessId });
	const getUsersPerShift = useGetUsersPerShiftQuery(usersPerShiftArgs, { refetchOnMountOrArgChange: true });
	const [curatedUsersPerShiftRows, setUsersPerShiftRows] = useState([]);
	const getShiftById = useGetOneShiftQuery(shiftId, { refetchOnMountOrArgChange: true });
	const getBusinessInfo = useGetBusinessInfoQuery(businessId, { refetchOnMountOrArgChange: true });
	const [shift, setShift] = useState<IShift>({});
	const [business, setBusiness] = useState<IBusinessInfo>({});
	const [byLine, setByLine] = useState<string>("");
	const [userShiftSlotsArgs, setUserShiftSlotArgs] = useState<IUserShiftSlotArg>({ startDate: moment(dayValue).format("yyyy-MM-DD"), businessId });
	const getUserShiftSlots = useGetUserShiftSlotsQuery(userShiftSlotsArgs, { refetchOnMountOrArgChange: true });
	const [daySlots, setDaySlots] = useState<Array<IUserShiftSlotDay>>([]);

	const defaultValues: IBooking = {
		id: undefined,
		startDate: "",
		endDate: "",
		statusId: 1,
		notes: ""
	};

	const schema = z.object({
		id: z.number().optional(),
		startDate: z.string(),
		endDate: z.string(),
		statusId: z.number().min(1),
		notes: z.string().min(1)
	});

	const methods = useForm({
		mode: "onChange",
		defaultValues: defaultValues,
		resolver: zodResolver(schema)
	});

	const { handleSubmit, control, formState } = methods;
	const { isValid, errors, isDirty } = formState;

	useEffect(() => {
		const { data: rawData, isLoading, isSuccess, isError, error, isFetching } = getUsersPerShift;

		dispatch(showLoading(isLoading));
		dispatch(showLoading(isFetching));

		if (isSuccess && rawData) {
			rawData.map((data) => {
				setUsersPerShiftRows((state) => [
					...state,
					{
						userName: data.userName,
						category: data.subCategory?.length > 0 ? data.subCategory : data.category,
						hasBookings: UsersPerShiftTableCustomIcons(data.hasBookings)
					}
				]);
			});
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${userShiftsTranslation.t("USERS_PER_SHIFT")}`, "error")));
		}

		return () => setUsersPerShiftRows([]);
	}, [getUsersPerShift]);

	useEffect(() => {
		const { data: rawData, isLoading, isSuccess, isError, error, isFetching } = getShiftById;

		dispatch(showLoading(isLoading));
		dispatch(showLoading(isFetching));

		if (isSuccess && rawData) {
			setShift(rawData);
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${shiftsTranslation.t("SHIFTS")}`, "error")));
		}

		return () => setShift({});
	}, [getShiftById]);

	useEffect(() => {
		const { data: rawData, isLoading, isSuccess, isError, error, isFetching } = getBusinessInfo;

		dispatch(showLoading(isLoading));
		dispatch(showLoading(isFetching));

		if (isSuccess && rawData) {
			setBusiness(rawData);
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("BUSINESS_INFO")}`, "error")));
		}

		return () => setBusiness({});
	}, [getBusinessInfo]);

	useEffect(() => {
		const { data: rawData, isLoading, isSuccess, isError, error, isFetching } = getUserShiftSlots;

		dispatch(showLoading(isLoading));
		dispatch(showLoading(isFetching));

		if (isSuccess && rawData) {
			rawData.map((shiftSlot) => {
				setDaySlots((state) => {
					return [...state, { dailyDate: moment(shiftSlot.dailyDate, "yyyy-MM-DD").toDate(), isFilled: shiftSlot.isFilled }];
				});
			});
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${userShiftsTranslation.t("USER_SHIFTS")}`, "error")));
		}

		return () => setDaySlots([]);
	}, [getUserShiftSlots]);

	useEffect(() => {
		let timeFlag = "";
		if (business) {
			if (isDay) {
				timeFlag = `Day (${SelectListShiftsShort[business.dayShiftStartHour]?.label} - ${SelectListShiftsShort[business.dayShiftEndHour]?.label})`;
			}

			if (isNight) {
				timeFlag = `Night (${SelectListShiftsShort[business.nightShiftStartHour]?.label} - ${SelectListShiftsShort[business.nightShiftEndHour]?.label})`;
			}
			setByLine(`${business.area} - ${shift.businessPhaseName} - ${shift.name} - ${timeFlag}`);
		} else {
			setByLine("");
		}
		return () => setByLine("");
	}, [shift, business]);

	useEffect(() => {
		setTitle(moment(dayValue).format("Do MMMM YYYY"));
		setUsersPerShiftArgs((state) => ({ ...state, day: moment(dayValue).format("yyyy-MM-DD") }));
	}, [dayValue]);

	return (
		<CustomFormDialog
			isDirty={isDirty}
			isValid={isValid}
			actions={
				bookingId && status === BookingStatus.REQUESTED
					? [
							{
								title: bookingTranslation.t("CANCEL_BOOKING"),
								onClick: () => {
									dispatch(openDialog({ children: <BookingCancelDialog bookingId={bookingId} /> }));
								},
								forceEnable: true
							}
						]
					: [
							{ title: constantTranslation.t("CANCEL"), onClick: () => dispatch(closeDialog()), variant: "outlined", forceEnable: true },
							{ title: userShiftsTranslation.t("WAITING_LIST"), onClick: handleSubmit((data) => console.log(data)) },
							{ title: userShiftsTranslation.t("REQUEST_1_DAY"), onClick: handleSubmit((data) => console.log(data)) }
						]
			}
			title={title}
			subtitle={byLine}
		>
			<div className="flex flex-col md:flex-row gap-12 w-[80vw] md:w-[70vw] lg:w-[60vw] xl:w-[40vw] px-12">
				<div className="flex flex-col gap-12">
					{curatedUsersPerShiftRows.length > 0 ? (
						<>
							<Typography className="font-bold">{userShiftsTranslation.t("USER_SHIFT_DIALOG_BOX_TITLE")}</Typography>
							<div className="flex flex-row gap-12 justify-center py-8">
								{daySlots.map((daySlot) => {
									const date = moment(daySlot.dailyDate).date();
									return (
										<button
											key={date}
											style={{ backgroundColor: daySlot.isFilled ? CARDINAL_500 : VIOLET_500 }}
											className="h-32 w-32 flex flex-row items-center justify-center rounded-full text-white cursor-pointer"
											onClick={(e) => {
												e.preventDefault();
												setDay(daySlot.dailyDate);
											}}
										>
											<span>{date}</span>
										</button>
									);
								})}
							</div>
							<CustomTable
								tableLabel={userShiftsTranslation.t("TEAM_TABLE")}
								showId={false}
								tableHeadings={userPerShiftHeadings()}
								hasActions={false}
								t={userShiftsTranslation.t}
								tableRows={curatedUsersPerShiftRows}
							/>
						</>
					) : (
						""
					)}
				</div>
				<div className="flex flex-col w-full gap-12 px-12 pb-24">
					{bookingId ? (
						<BookingInformationDialog
							bookingId={bookingId}
							userShiftFlag={true}
						/>
					) : (
						<>
							<Typography className="font-bold">{bookingTranslation.t("NOTES_DIALOG_BOX_TITLE")}</Typography>
							<CustomTextField
								control={control}
								name="notes"
								label={`${bookingTranslation.t("NOTES")}`}
								errors={errors}
								multiline
								rows={width < 960 ? 10 : 20}
								InputLabelProps={{
									shrink: true
								}}
								dialog={true}
								calendarSpace={true}
								placeholder={bookingTranslation.t("NOTES_PLACEHOLDER")}
							/>
						</>
					)}
				</div>
			</div>
		</CustomFormDialog>
	);
};

export default UserShiftsDialogForm;
