import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import interactionPlugin from "@fullcalendar/interaction";
import { DateSelectArg, DatesSetArg, EventClickArg, EventContentArg } from "@fullcalendar/core";
import { useEffect, useRef, useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { CONSTANTS, USER_SHIFTS_PAGE } from "app/constants/TranslationNamespaces";
import { IUserShiftsArg } from "./UserShiftsModel";
import { GetUserShiftsApiResponse, useGetUserShiftsByMonthQuery } from "./UserShiftsApi";
import { FullCalendarEvent } from "../shifts/tabs/shift-pattern/ShiftPatternModel";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";
import moment from "moment";
import { userShiftColors } from "./constants/userShiftsColors";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { Tooltip, Typography, IconButton, Box, FormControlLabel, Checkbox } from "@mui/material";
import { motion } from "framer-motion";
import FuseSvgIcon from "@fuse/core/FuseSvgIcon";
import clsx from "clsx";
import { userShiftTypes } from "./constants/userShiftTypes";
import { openDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import UserShiftsDialogForm from "./user-shifts-dialog-form/UserShiftsDialogForm";
import { useAppSelector } from "app/store/hooks";

const UserShiftsContent = () => {
	const loggedInUser = useAppSelector((state) => state.user);
	const constantTranslation = useTranslation(CONSTANTS);
	const userShiftsTranslation = useTranslation(USER_SHIFTS_PAGE);
	const dispatch = useDispatch();
	const [currentDate, setCurrentDate] = useState<DatesSetArg>();
	const calendarRef = useRef<FullCalendar>(null);
	const calendarApi = () => calendarRef.current.getApi();
	const [fetchArgs, setFetchArgs] = useState<Array<IUserShiftsArg>>([
		{ month: new Date().getMonth() + 1, year: new Date().getFullYear(), businessId: "1" },
		{ month: new Date().getMonth() + 2, year: new Date().getFullYear(), businessId: "1" },
		{ month: new Date().getMonth() + 3, year: new Date().getFullYear(), businessId: "1" }
	]);
	const getUserShiftsPrevious = useGetUserShiftsByMonthQuery(fetchArgs[0], { refetchOnMountOrArgChange: true });
	const getUserShiftsCurrent = useGetUserShiftsByMonthQuery(fetchArgs[1], { refetchOnMountOrArgChange: true });
	const getUserShiftsNext = useGetUserShiftsByMonthQuery(fetchArgs[2], { refetchOnMountOrArgChange: true });
	const [curatedEvents, setCuratedEvents] = useState<Array<FullCalendarEvent>>([]);
	const [filteredEvents, setFilteredEvents] = useState<Array<FullCalendarEvent>>([]);
	const [dayFilter, setDayFilter] = useState<boolean>(true);
	const [nightFilter, setNightFilter] = useState<boolean>(true);
	const [holidayFilter, setHolidayFilter] = useState<boolean>(true);

	const handleDates = (rangeInfo: DatesSetArg) => {
		setCurrentDate(rangeInfo);
	};

	const handleDateSelect = (selectInfo: DateSelectArg) => {
		const shiftExists = curatedEvents.filter((event) => moment(selectInfo.start).toISOString() === event.start)[0];
		if (shiftExists) {
			dispatch(
				openDialog({
					children: (
						<UserShiftsDialogForm
							day={moment(selectInfo.start).format("yyyy-MM-DD")}
							businessId="1"
							shiftId={loggedInUser.data.shiftId.toString()}
							bookingId={shiftExists.extendedProps.bookingId?.toString()}
							status={shiftExists.extendedProps.status}
							endDate={shiftExists.extendedProps.endDate}
							isDay={shiftExists.extendedProps.hasDayShift}
							isNight={shiftExists.extendedProps.hasNightShift}
							isHoliday={shiftExists.extendedProps.onHoliday}
						/>
					)
				})
			);
		}
	};

	const handleEventClick = (clickInfo: EventClickArg) => {
		const shiftExists = curatedEvents.filter((event) => moment(clickInfo.event.start).toISOString() === event.start)[0];
		if (shiftExists) {
			dispatch(
				openDialog({
					children: (
						<UserShiftsDialogForm
							day={moment(clickInfo.event.start).format("yyyy-MM-DD")}
							businessId="1"
							shiftId={loggedInUser.data.shiftId.toString()}
							bookingId={shiftExists.extendedProps.bookingId?.toString()}
							status={shiftExists.extendedProps.status}
							endDate={shiftExists.extendedProps.endDate}
							isDay={shiftExists.extendedProps.hasDayShift}
							isNight={shiftExists.extendedProps.hasNightShift}
							isHoliday={shiftExists.extendedProps.onHoliday}
						/>
					)
				})
			);
		}
	};

	const handleSelectAllow = (selectedEvent) => {
		let startDate = selectedEvent.start;
		let endDate = selectedEvent.end;
		endDate.setSeconds(endDate.getSeconds() - 1);
		return startDate.getDate() === endDate.getDate();
	};

	const curateData = (data: GetUserShiftsApiResponse) => {
		data.map((val) => {
			let index = 0;

			if (val.hasDayShift) {
				index = 0;
			}

			if (val.hasNightShift) {
				index = 1;
			}

			if (val.onHoliday) {
				index = 2;
			}
			setCuratedEvents((state) => {
				if (state.filter((st) => st.id === val.id.toString()).length > 0) {
					return state;
				}

				return [
					...state,
					{
						id: val.id.toString(),
						title: "",
						allDay: true,
						start: moment(val.day, "yyyy-MM-DD").toISOString(),
						end: moment(val.day, "yyyy-MM-DD").add(1, "days").toISOString(),
						extendedProps: {
							label: userShiftTypes[index],
							color: userShiftColors[index],
							shiftPatternId: val.id.toString(),
							hasDayShift: val.hasDayShift,
							hasNightShift: val.hasNightShift,
							onHoliday: val.onHoliday,
							bookingId: val.bookingId,
							endDate: val.endDate,
							status: val.status
						}
					}
				];
			});
		});
	};

	useEffect(() => {
		const { data: rawData, isLoading, isSuccess, isError, error, isFetching } = getUserShiftsPrevious;

		dispatch(showLoading(isLoading));
		dispatch(showLoading(isFetching));

		if (isSuccess && rawData) {
			curateData(rawData);
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${userShiftsTranslation.t("USER_SHIFTS")}`, "error")));
		}
	}, [getUserShiftsPrevious]);

	useEffect(() => {
		const { data: rawData, isLoading, isSuccess, isError, error, isFetching } = getUserShiftsCurrent;

		dispatch(showLoading(isLoading));
		dispatch(showLoading(isFetching));

		if (isSuccess && rawData) {
			curateData(rawData);
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${userShiftsTranslation.t("USER_SHIFTS")}`, "error")));
		}
	}, [getUserShiftsCurrent]);

	useEffect(() => {
		const { data: rawData, isLoading, isSuccess, isError, error, isFetching } = getUserShiftsNext;

		dispatch(showLoading(isLoading));
		dispatch(showLoading(isFetching));

		if (isSuccess && rawData) {
			curateData(rawData);
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${userShiftsTranslation.t("USER_SHIFTS")}`, "error")));
		}
	}, [getUserShiftsNext]);

	useEffect(() => {
		if (currentDate?.start) {
			setFetchArgs([
				{ month: currentDate.start.getMonth() + 1, year: currentDate.start.getFullYear(), businessId: "1" },
				{ month: currentDate.start.getMonth() + 2, year: currentDate.start.getFullYear(), businessId: "1" },
				{ month: currentDate.start.getMonth() + 3, year: currentDate.start.getFullYear(), businessId: "1" }
			]);
		}
	}, [currentDate]);

	useEffect(() => {
		if (curatedEvents.length > 0) {
			let filteredEvents = [];
			if (dayFilter && nightFilter && holidayFilter) {
				filteredEvents = curatedEvents;
			} else if (dayFilter && nightFilter) {
				filteredEvents = curatedEvents.filter((ev) => ev.extendedProps.hasDayShift === true || ev.extendedProps.hasNightShift === true);
			} else if (dayFilter && holidayFilter) {
				filteredEvents = curatedEvents.filter((ev) => ev.extendedProps.hasDayShift === true || ev.extendedProps.onHoliday === true);
			} else if (holidayFilter && nightFilter) {
				filteredEvents = curatedEvents.filter((ev) => ev.extendedProps.hasNightShift === true || ev.extendedProps.onHoliday === true);
			} else {
				if (dayFilter) {
					filteredEvents = curatedEvents.filter((ev) => ev.extendedProps.hasDayShift === true);
				}
				if (nightFilter) {
					filteredEvents = curatedEvents.filter((ev) => ev.extendedProps.hasNightShift === true);
				}
				if (holidayFilter) {
					filteredEvents = curatedEvents.filter((ev) => ev.extendedProps.onHoliday === true);
				}
			}
			setFilteredEvents(filteredEvents);
		}
	}, [curatedEvents, dayFilter, nightFilter, holidayFilter]);

	return (
		<div className="flex flex-col w-full">
			<div className="flex flex-col md:flex-row space-between md:items-center w-full">
				<div className="flex flex-row p-12 items-center mr-auto">
					<Typography className="text-2xl font-semibold tracking-tight whitespace-nowrap mx-16">{currentDate?.view.title}</Typography>
					<div className="flex items-center">
						<Tooltip title="Previous">
							<IconButton
								aria-label="Previous"
								onClick={() => calendarApi().prev()}
							>
								<FuseSvgIcon size={20}>heroicons-solid:chevron-left</FuseSvgIcon>
							</IconButton>
						</Tooltip>
						<Tooltip title="Next">
							<IconButton
								aria-label="Previous"
								onClick={() => calendarApi().next()}
							>
								<FuseSvgIcon size={20}>heroicons-solid:chevron-right</FuseSvgIcon>
							</IconButton>
						</Tooltip>
						<Tooltip title="Today">
							<div>
								<motion.div
									initial={{ scale: 0 }}
									animate={{ scale: 1, transition: { delay: 0.3 } }}
								>
									<IconButton
										aria-label="today"
										onClick={() => calendarApi().today()}
										size="large"
									>
										<FuseSvgIcon>heroicons-outline:calendar</FuseSvgIcon>
									</IconButton>
								</motion.div>
							</div>
						</Tooltip>
					</div>
				</div>
				<div className="flex flex-col sm:flex-row md:items-center px-24 pb-24 md:p-12 md:px-0">
					<FormControlLabel
						label={
							<div className="flex flex-row items-center">
								<div
									className="w-12 h-12 rounded-full mr-8"
									style={{ backgroundColor: userShiftColors[0] }}
								></div>
								Day
							</div>
						}
						control={
							<Checkbox
								checked={dayFilter}
								onChange={(_, checked) => setDayFilter(checked)}
							/>
						}
					/>
					<FormControlLabel
						label={
							<div className="flex flex-row items-center">
								<div
									className="w-12 h-12 rounded-full mr-8"
									style={{ backgroundColor: userShiftColors[1] }}
								></div>
								Night
							</div>
						}
						control={
							<Checkbox
								checked={nightFilter}
								onChange={(_, checked) => setNightFilter(checked)}
							/>
						}
					/>
					<FormControlLabel
						label={
							<div className="flex flex-row items-center">
								<div
									className="w-12 h-12 rounded-full mr-8"
									style={{ backgroundColor: userShiftColors[2] }}
								></div>
								Holiday
							</div>
						}
						control={
							<Checkbox
								checked={holidayFilter}
								onChange={(_, checked) => setHolidayFilter(checked)}
							/>
						}
					/>
				</div>
			</div>
			<FullCalendar
				plugins={[dayGridPlugin, interactionPlugin]}
				headerToolbar={false}
				initialView="dayGridMonth"
				editable
				selectable={true}
				dragScroll={true}
				ref={calendarRef}
				initialDate={new Date()}
				datesSet={handleDates}
				selectLongPressDelay={100}
				eventLongPressDelay={100000}
				selectAllow={handleSelectAllow}
				select={handleDateSelect}
				events={filteredEvents.length > 0 ? filteredEvents : curatedEvents}
				eventContent={(eventInfo: EventContentArg & { event: Event }) => {
					return (
						<Box
							sx={{ backgroundColor: eventInfo.event.extendedProps.color }}
							className={clsx("flex items-center w-full rounded-4 py-12")}
						/>
					);
				}}
				eventClick={handleEventClick}
			/>
		</div>
	);
};

export default UserShiftsContent;
