import FuseSvgIcon from "@fuse/core/FuseSvgIcon";

export const UsersPerShiftTableCustomIcons = (isChecked) => {
	if (isChecked) {
		return (
			<FuseSvgIcon
				sx={{ color: "primary.main" }}
				size={24}
			>
				material-solid:check_box
			</FuseSvgIcon>
		);
	} else {
		return (
			<FuseSvgIcon
				sx={{ color: "primary.main" }}
				size={24}
			>
				material-outline:check_box_outline_blank
			</FuseSvgIcon>
		);
	}
};
